.container {
  background-color: #f5f5f5;
  min-height: 100vh;
  padding: 0 25vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.comingsoon {
  background-color: rgb(12, 12, 12);
  width: 75vw;
  height: 75vh;
  display: flex;
  flex-direction: column;
  align-self: center;
  justify-content: center;
  border-radius: 5vh;
  color: #f5f5f5;
}
